<template>
  <v-card class="overflow-hidden">
    <v-app-bar
      fixed
      color="black"
      dark
    >
      <v-app-bar-nav-icon @click="toggleNavigationDrawer"></v-app-bar-nav-icon>
      <v-toolbar-title>{{ pageCmpt }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu v-show="itemsCmpt.length > 0" offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          v-bind="attrs"
          v-on="on"
          icon
        >
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-list :min-width="200">
        <v-list-item
          v-for="(item, index) in itemsCmpt"
          :key="index"
          @click="callAction(item.action)"
        >
          <v-list-item-title>
            <v-icon class="mr-2" small>
              {{ item.icon }}
            </v-icon> {{ item.title }}
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    </v-app-bar>
    <v-container>
      <div class="mt-12">
        <slot></slot>
      </div>
    </v-container>
    <navigation-drawer ref="navigationDrawer" />
  </v-card>
</template>

<script>
import NavigationDrawer from './NavigationDrawer'

import UserMixin from '@/mixins/UserMixin'

export default {
  components: { NavigationDrawer },
  mixins: [ UserMixin ],
  data: () => ({
    items: []
  }),
  computed: {
    itemsCmpt () {
      let items = this.$route.meta.rightTopBar ? [...this.$route.meta.rightTopBar] : []
      if (this.isUserAutenticated()) {
        items.push({ title: 'Sair', action: 'logout', icon: 'logout' }) 
      }
      return items
    },
    pageCmpt () {
      return this.$route.meta.pageName || 'Meu Barbeiro'
    }
  },
  methods: {
    callAction (action) {
      if (action === 'logout' ) this.logoutUser()
      else this.$emit('callAction', action)
    },
    toggleNavigationDrawer () {
      this.$refs.navigationDrawer.toggleNavigationDrawer()
    }
  }
}
</script>